 //Variables
@import "variables";

 .navbar{
   margin-bottom: 0px;
 }
 .no-margin{
   margin: 0px;
 }
 .top-space{
   margin-top: 1em;
 }
 .no-padding{
   padding: 0px;
 }
 .large-padding{
   padding: 10px 15px;
 }
 .big-padding{
   padding: 30px;
 }
 .no-transform{
   text-transform: none;
 }
 .blue-grey{
   background-color: #37474f;
 }
 .white{
   background-color: #ffffff;
 }
 .white-text{
   color: #ffffff;
 }
 .red-text{
   color: rgb(200, 50, 50);
 }
 .inline-block{
   display: inline-block;
 }
 .table{
   background-color: #ffffff;
 }
 .absolute{
   position: absolute;
 }
 .floating{
   position: fixed;
   bottom: 2em;
   right: 2em;
   z-index: 100;
 }
 .product{
   width: 600px;
   max-width: 100%;
   padding: 1em;
   position: relative;
   h1{
     padding-bottom: 10px;
     border-bottom: solid 1px;
   }
   .actions{
     right: 1em;
     top: 10px;
   }
 }

 .sale-data{
   padding: 1em;
   text-align: center;
   border-right: solid 1px #222;
   span{
     display: block;
     font-size: 2.2em;
     color: #3f51b5;
   }
 }
 //Status order
 .creado{
   background-color: #d4e157;
 }
